<template>
  <div>
    <van-nav-bar
      title="修改手机号码"
      left-text="返回"
      left-arrow
      @click-left="$toPage(-1)"
    >
      <van-icon name="arrow-left" size="20px" color="#666666" slot="left" />
    </van-nav-bar>

    <!-- <div  :class="title_tag" >
      <div class="title_0">1.验证原号码</div>
      <div class="title_1"><span></span></div>
      <div class="title_2">2.绑定新手机</div>
    </div> -->

    <van-swipe indicator-color="white" :touchable="false" :show-indicators="false" ref='step_index' class="swipe_box" >
      <van-swipe-item>
        <van-field
          class="input_box"
          v-model="oldUserPhoneTxt"
          label="原手机号码"
          type="tel"
          readonly
          size="large"
        />
        <van-field
          class="input_box"
          v-model="oldSms"
          center
          clearable
          size="large"
          label="短信验证码"
          placeholder="输入短信验证码"
          type="tel"
        >
          <van-button slot="button" size="small" type="primary" @click="get_sms_0">
            发送验证码 {{ time_0 }}
          </van-button>
        </van-field>
        <van-field
          class="input_box"
          v-model="newUserPhone"
          label="新手机号码"
          placeholder="输入新手机号码"
          type="tel"
          size="large"
          :error-message="message"
          @blur="blurPhone"
          @focus="focusPhone"
        />
        <div @click="fun_btn_Accomplish" class="fun_btn_Accomplish">完成</div>
        <p class="in_point">温馨提示：</p>
        <p class="in_point">手机号码修改成功后需要使用新的手机号码进行登录。</p>
      </van-swipe-item>

      <!-- <van-swipe-item>
        <van-field
          class="input_box"
          v-model="newUserPhoneTxt"
          label="新手机号码"
          placeholder="输入新手机号码"
          type="tel"
          :error-message="message"
        />
        <van-field
          class="input_box"
          v-model="newSms"
          center
          clearable
          label="短信验证码"
          placeholder="输入短信验证码"
          type="tel"
        >
          <van-button slot="button" size="small" type="primary" @click="get_sms">
            发送验证码 {{ time_1 }}
          </van-button>
        </van-field>
        <div @click="fun_btn_Accomplish" class="btn_next_step">完成</div>

        <p class="in_point">温馨提示：</p>
        <p class="in_point">手机号码修改成功后需要使用新的手机号码进行登录。</p>
      </van-swipe-item> -->
    </van-swipe>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        oldUserPhone:'',
        oldUserPhoneTxt:'',
        oldSms:'',
        newUserPhone:'',
        title_tag:'title_active_0',
        flag_0: true,
        time_0: '',
        message:''
        //flag_1: true,
        //time_1: ''
      }
    },
    mounted(){
      this.get_store_oldUserPhone();
    },
    methods: {
      fun_btn_Accomplish(){
        if(this.regist_test()){
          JNB.query({
            serverName: "userServer",
            cls: "LoginRegistrationModule",
            method: "changeUserInfo",
            params: {
              oldPhone: this.oldUserPhone,
              newPhone: this.newUserPhone,
              smsCode: this.oldSms
            },
            callback:(data) => {
              const dataStr = JSON.parse(data)
              if (dataStr.code === 200) {
                this.$toast(dataStr.data);
                this.$router.push({
                  name: 'login'
                })
              }else{
                this.$toast(dataStr.message)
              }
            },
          }) 
        }
      },
      //  表单验证
      regist_test() {
        if (!this.newUserPhone || !this.newUserPhone){
          this.$toast('新手机号码不能为空！')
          return false
        }
        if (!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.newUserPhone))){
          this.$toast('请输入正确的手机号码！')
          return false
        }
        if (!this.oldSms) {
          this.$toast('请填写验证码！')
          return false
        }
        return true
      },

      get_store_oldUserPhone(){
        JNB.query({
          serverName: "userServer",
          cls: "LoginRegistrationModule",
          method: "getStoreSession",
          callback:(data) => {
            const dataStr = JSON.parse(data)
            if (dataStr.code === 200) {
              this.oldUserPhone=dataStr.data.phone;
              this.oldUserPhoneTxt=`${this.oldUserPhone.substring(0,3)}****${this.oldUserPhone.substring(7,11)}`;
            } else {
              this.$toast(dataStr.message)
            }
          },
        })
      },
      get_sms_0:function(){


          this.time_0 = 60;
          var timer_0 = setInterval(() => {
            this.time_0--
            if (this.time_0 <= 0) {
              clearInterval(timer_0)
              this.time_0 = '';

            }
          }, 1000)
          JNB.query({
            serverName: "userServer",
            cls: "LoginRegistrationModule",
            method: "obtainVerificationCode",
            params: {
              phone: this.oldUserPhone,
              type:3
            },
            callback:(data) => {
              const dataStr = JSON.parse(data)
              if (dataStr.code === 200) {
                this.$toast(dataStr.data)
              } else {
                this.$toast(dataStr.data)
              }
            },
          })

      },
      //  手机号码验证
      blurPhone() {
        JNB.query({
          serverName: "userServer",
          cls: "LoginRegistrationModule",
          method: "checkPhoneExist",
          params: {
            phone: this.newUserPhone
          },
          callback:(data) => {
            const dataStr = JSON.parse(data)
            if (dataStr.code !== 200) {
              this.message = dataStr.message
            }
          },
        })
      },
      focusPhone() {
        this.message = ''
      }
    }
  }
</script>

<style scoped>
  .title_active_0{
    height: 80px;
    line-height: 80px;
    display: flex;
    position: relative;
    border-bottom: 1px solid #f5f5f5;
    border-top: 1px solid #f5f5f5;
    font-size: 24px;
    justify-content: space-between;
    overflow: hidden;
    background-color: #fff;
  }
  .title_active_1{
    height: 80px;
    line-height: 80px;
    display: flex;
    position: relative;
    border-bottom: 1px solid #f5f5f5;
    border-top: 1px solid #f5f5f5;
    font-size: 24px;
    justify-content: space-between;
    overflow: hidden;
    background-color: #f8f8f8;
  }
  .title_active_0 .title_0{
    color: red;
  }
  .title_active_1 .title_0{
    background-color: #FFFFFF;
  }
  .title_0{
    width: 355px;
    text-align: center;
    background-color: #f8f8f8;
  }
  .title_active_1 .title_1 span{
    border-color:transparent transparent transparent #fff;
  }
  .title_1{
    width:0;
    height:0;
    border-width:60px 0 60px 40px;
    border-style:solid;
    border-color:transparent transparent transparent #f5f5f5;
    position:relative;
    margin-top: -20px;
  }
  .title_1 span{
    display:block;
    width:0;
    height:0;
    border-width:58px 0 58px 38.6px;
    border-style:solid;
    border-color:transparent transparent transparent #f8f8f8;
    position:absolute;
    top:-58px;
    left:-41px;
  }
  .title_2{
    width: 355px;
    text-align: center;
  }
  .input_box{
    border-bottom: 1px solid #f5f5f5;
  }
  .swipe_box{
    border-top: 1px solid #f5f5f5;
  }
  .fun_btn_Accomplish{
    height: 80px;
    line-height: 80px;
    text-align: center;
    width: 600px;
    border-radius: 40px;
    background-color: #1e90ff;
    margin: 40px auto;
    color: #ffffff;
  }
  .in_point{
    padding: 0 30px;
    font-size: 20px;
    margin: 0;
    height: 40px;
    line-height: 40px;
  }
  .fun_btn_Accomplish[data-v-8b92d0bc]{
    background-color: #FF4444;
  }
</style>
